import _ from 'lodash';
import { Optional } from 'utility-types';
import { KeyValue, TraceEvent } from './types';

function mockStartEpoch(timestamp: number, epochId: number): TraceEvent {
  return {
    timestamp,
    eventName: 'goto_new_epoch',
    keyframe: {
      epochId,
    },
  };
}

function mockStartRound(timestamp: number, roundId: number): TraceEvent {
  return {
    timestamp,
    eventName: 'goto_new_round',
    keyframe: {
      roundId,
    },
  };
}

const steps = ['propose', 'prevote', 'precommit', 'commit'];
function mockStartStep(timestamp: number, step: number): TraceEvent {
  const stepName = steps[step];
  return {
    eventName: stepName,
    timestamp,
    keyframe: {
      stepName,
    },
  };
}

const vs: string[] = [
  'check',
  'get',
  'receive',
  'request',
  'start',
  'validate',
];
const ns: string[] = ['epoch', 'tx', 'proposal', 'vote', 'lock'];
const vn: string[] = vs.flatMap(v => ns.map(n => `${v}_${n}`));

const ts: KeyValue[] = [
  { address: '0x10000000000000000000000000000000000000' },
  {
    address: '0x10000000000000000000000000000000000000',
    lock: '0x10000000000000000000000000000000000000',
  },
  {
    key: Math.random(),
  },
  {
    tx: '0x10000000000000000000000000000000000000',
    proposal: '0x10000000000000000000000000000000000000',
  },
];

function mockTag(): KeyValue | undefined {
  return _.sample(ts);
}

function mockCustomEvent(timestamp: number): TraceEvent {
  return {
    timestamp,
    eventName: _.sample(vn) as string,
    tag: mockTag(),
  };
}

interface MockOptions {
  startTime: number;
  startEpochId: number;
  epochCount: number;
}

class Mock {
  private readonly options: MockOptions;

  private time: number;
  private epoch: number;
  private round: number;
  private step: number;

  private readonly events: TraceEvent[];

  constructor(options?: Optional<MockOptions>) {
    this.events = [];
    this.options = this.initialOptions(options);

    const { startTime, startEpochId } = this.options;
    this.time = startTime;
    this.epoch = startEpochId;
    this.round = 0;
    this.step = 0;
  }

  initialOptions(options?: Optional<MockOptions>): MockOptions {
    return Object.assign(
      {
        startEpochId: _.random(0, 10000000),
        epochCount: 1200,
        startTime: _.random(+new Date(2016, 1, 1), Date.now()),
      },
      options,
    ) as MockOptions;
  }

  private consumeTime(max = 300) {
    this.time += _.random(0, max);
  }

  private mockCustomEvent(n: number = 2) {
    let i = _.random(0, n);
    while (i--) {
      this.consumeTime(30);
      this.events.push(mockCustomEvent(this.time));
    }
  }

  mock(): TraceEvent[] {
    let count = this.options.epochCount;

    while (count--) {
      this.mockCustomEvent();
      if (Math.random() < 0.05) {
        this.consumeTime(3000);
        this.epoch++;
        continue;
      }
      this.consumeTime();
      this.events.push(mockStartEpoch(this.time, this.epoch));

      const round = _.random(0, 6);
      while (this.round <= round) {
        this.mockCustomEvent();

        if (Math.random() < 0.05) {
          this.consumeTime();
          this.round++;
          continue;
        }
        this.consumeTime();
        this.events.push(mockStartRound(this.time, this.round));

        const step = round === this.round ? 3 : _.random(0, 3);
        while (this.step <= step) {
          this.mockCustomEvent();

          this.consumeTime();
          this.events.push(mockStartStep(this.time, this.step));

          this.step++;
        }

        this.step = 0;
        this.round++;
      }
      this.round = 0;
      this.epoch++;
    }

    return this.events;
  }
}

export { Mock };
