import React from 'react';
import { useStoreActions, useStoreState } from '../store';
import { Profile } from './Profile';
import { JSONLogReader } from '../component/FileZone';

export const Guide = () => {
  const eventLoaded = useStoreState(state => state.tracer.eventLoaded);
  const loadEvents = useStoreActions(actions => actions.tracer.loadEvents);

  if (eventLoaded) return <Profile />;
  return <JSONLogReader onLoad={loadEvents} />;
};
