import React from 'react';
import { Drawer, Table } from 'antd';
import styled from 'styled-components';
import { useStoreActions, useStoreState } from '../store';
import { TraceEvent } from '../types';
import { EventDetail } from './EventDetail';
import { formatDate } from '../helper';

const EventName = styled.div`
  cursor: pointer;
`;

interface EventListProp {}

export const EventList = (props: EventListProp) => {
  const { slice, activeEpoch, activeEvent } = useStoreState(
    state => state.tracer,
  );
  const { activateEvent, inactivateEvent } = useStoreActions(
    actions => actions.tracer,
  );
  if (activeEpoch === null) return null;
  const epoch = slice.epoch(activeEpoch);
  const epochEvents = epoch.events();

  const columns = [
    {
      key: 'Name',
      title: 'Name',
      dataIndex: 'eventName',
      render: (name: string, record: TraceEvent, index: number) => (
        <EventName onClick={() => activateEvent(index + epoch.startIndex)}>
          {name}
        </EventName>
      ),
    },
    {
      key: 'Timestamp',
      title: 'Timestamp',
      dataIndex: 'timestamp',
      render: (timestamp: number) => formatDate(timestamp),
    },
  ];

  return (
    <div style={{ position: 'relative', overflow: 'hidden' }}>
      <Table
        size="small"
        pagination={false}
        rowKey={(record: TraceEvent, i) => String(i)}
        dataSource={epochEvents}
        columns={columns}
        scroll={{ y: 300 }}
      />

      <Drawer
        placement="right"
        width={400}
        onClose={() => inactivateEvent()}
        visible={!!activeEvent}
        mask={false}
        getContainer={false}
        style={{ position: 'absolute' }}
      >
        <EventDetail event={activeEvent as TraceEvent} />
      </Drawer>
    </div>
  );
};
