import {
  action,
  Action,
  computed,
  Computed,
  createStore,
  createTypedHooks,
} from 'easy-peasy';
import { TraceEvent } from '../types';
import { isEmpty } from 'lodash';
import { Events } from '../event';

type NestedEvent = Map<string, Map<string, TraceEvent>>;

interface TracerModel {
  events: TraceEvent[];
  activeIndex: number | null;
  activeEpoch: number | null;

  slice: Computed<TracerModel, Events>;
  eventLoaded: Computed<TracerModel, boolean>;
  activeEvent: Computed<TracerModel, TraceEvent | null>;

  activateEpoch: Action<TracerModel, number>;
  inactivateEpoch: Action<TracerModel>;
  loadEvents: Action<TracerModel, TraceEvent[]>;
  activateEvent: Action<TracerModel, number>;
  inactivateEvent: Action<TracerModel>;
}

interface StoreModel {
  tracer: TracerModel;
}

const storeModel: StoreModel = {
  tracer: {
    events: [],
    activeIndex: null,
    activeEpoch: null,

    slice: computed(state => {
      return new Events(state.events);
    }),
    eventLoaded: computed(state => !isEmpty(state.events)),
    activeEvent: computed(state =>
      state.activeIndex === null ? null : state.events[state.activeIndex],
    ),

    loadEvents: action((state, events) => {
      state.events = events;
    }),
    activateEvent: action((state, index) => {
      state.activeIndex = index;
    }),
    inactivateEvent: action(state => {
      state.activeIndex = null;
    }),

    activateEpoch: action((state, epochId) => {
      state.activeEpoch = epochId;
      state.activeIndex = null;
    }),
    inactivateEpoch: action(state => {
      state.activeEpoch = null;
    }),
  },
};

const typedHooks = createTypedHooks<StoreModel>();
export const store = createStore<StoreModel>(storeModel);

export const useStoreActions = typedHooks.useStoreActions;
export const useStoreDispatch = typedHooks.useStoreDispatch;
export const useStoreState = typedHooks.useStoreState;
