import React from 'react';
import { Row, Col, Statistic } from 'antd';
import _ from 'lodash';
import { useStoreState } from '../store';
import { EpochTimeline } from './EpochTimeline';

interface EpochOverviewProps {}

export const EpochOverview = (props: EpochOverviewProps) => {
  const { activeEpoch, slice } = useStoreState(state => state.tracer);

  if (activeEpoch === null) return null;

  const roundCount: number =
    (_.last(slice.epoch(activeEpoch).rounds())?.roundId ?? -1) + 1;

  return (
    <Row gutter={16} style={{ padding: '8px' }}>
      <Col span={6}>
        <Statistic title="Epoch" value={activeEpoch} />
      </Col>
      <Col span={6}>
        <Statistic title="Round usage" value={roundCount} />
      </Col>
      <Col span={12}>
        <EpochTimeline />
      </Col>
    </Row>
  );
};
