import _ from 'lodash';
import { KeyframeKey, TraceEvent } from './types';

export function indexOfKeyframe(
  events: TraceEvent[],
  key: KeyframeKey,
  value: string | number,
): number {
  // @ts-ignore
  return events.findIndex(event => event.keyframe?.[key] === value);
}

export function indexOfNextKeyframe(
  events: TraceEvent[],
  startIndex: number,
): number {
  return _.findIndex(events, event => 'keyframe' in event, startIndex + 1);
}

export function indexOfNextKeyframeByKey(
  events: TraceEvent[],
  startIndex: number,
  key: KeyframeKey,
) {
  return _.findIndex(
    events,
    event => !!(event.keyframe && key in event.keyframe),
    startIndex + 1,
  );
}

export function checkIsKeyframeOf(
  event: TraceEvent,
  key: KeyframeKey,
): boolean {
  const keyframe = event.keyframe;
  return !!(keyframe && key in keyframe);
}

export function valueOfKeyframe<T>(
  event: TraceEvent,
  key: KeyframeKey,
  defaults: T,
): T {
  const keyframe = event.keyframe;
  if (!keyframe || !(key in keyframe)) return defaults;
  // @ts-ignore
  return keyframe[key];
}

export function idOfEpochKeyframe(event: TraceEvent): number {
  return valueOfKeyframe(event, 'epochId', -1);
}

export function idOfRoundKeyframe(event: TraceEvent): number {
  return valueOfKeyframe(event, 'roundId', -1);
}

export function stepNameOfKeyframe(event: TraceEvent): string {
  return valueOfKeyframe(event, 'stepName', '');
}

export function eventsBetweenKeyframe(
  events: TraceEvent[],
  key: 'epochId' | 'roundId' | 'stepName',
  value: string | number,
): TraceEvent[] {
  const startIndex = indexOfKeyframe(events, key, value);
  if (startIndex === -1) return [];
  const endIndex = indexOfNextKeyframe(events, startIndex);

  return events.slice(startIndex, endIndex);
}

interface Slice {
  timeRange: () => [number, number] | null;
  events: () => TraceEvent[];
  nextSliceStartEvent: () => TraceEvent | null;
}

interface StepSlice extends Slice {
  parentSlice: () => RoundSlice;
  stepName: string;
}

interface RoundSlice extends Slice {
  readonly roundId: number;
  steps: () => StepSlice[] | null;
}

export interface EpochSlice extends Slice {
  readonly epochId: number;
  rounds: () => RoundSlice[] | null;
}

abstract class BaseEvents<T> implements Slice {
  readonly raw: TraceEvent[];
  readonly startIndex: number;
  readonly endIndex: number;
  abstract readonly parent: T;

  protected constructor(raw: TraceEvent[], ...sliceArgs: any) {
    this.raw = raw;
    const [start, end] = this.slice(raw, ...sliceArgs);
    this.startIndex = start;
    this.endIndex = end;
  }

  protected abstract slice(
    events: TraceEvent[],
    ...args: any
  ): [number, number];

  events = _.memoize<() => TraceEvent[]>(() => {
    if (this.startIndex === -1 || this.endIndex === -1) return [];
    return this.raw.slice(this.startIndex, this.endIndex + 1);
  });

  nextSliceStartEvent: () => TraceEvent | null = () => {
    if (this.startIndex === -1 || this.endIndex === -1) return null;
    const nextKeyframeIndex = indexOfNextKeyframe(this.raw, this.endIndex);
    if (nextKeyframeIndex === -1) return null;
    return this.raw[nextKeyframeIndex];
  };

  timeRange: () => [number, number] | null = () => {
    if (this.startIndex === -1 || this.endIndex === -1) return null;
    const nextKeyframe = this.nextSliceStartEvent();
    if (!nextKeyframe) return null;
    return [this.raw[this.startIndex].timestamp, nextKeyframe.timestamp];
  };
}

export class EpochEvents extends BaseEvents<Events> implements EpochSlice {
  readonly epochId: number;
  readonly parent: Events;

  constructor(raw: TraceEvent[], epochId: number, parent?: Events) {
    super(raw, epochId);
    this.epochId = epochId;
    this.parent = parent as Events;
  }

  protected slice(events: TraceEvent[], epochId: number): [number, number] {
    const start = indexOfKeyframe(events, 'epochId', epochId);
    const end = indexOfNextKeyframeByKey(events, start, 'epochId') - 1;
    return [start, end];
  }

  rounds(): RoundSlice[] | null {
    const epochEvents = this.events();
    if (_.isEmpty(epochEvents)) return null;

    const self = this;
    return epochEvents
      .filter(event => checkIsKeyframeOf(event, 'roundId'))
      .map<RoundSlice>(
        event => new RoundEvents(idOfRoundKeyframe(event), self),
      );
  }
}

export class RoundEvents extends BaseEvents<EpochEvents> implements RoundSlice {
  readonly roundId: number;
  readonly parent: EpochEvents;

  constructor(roundId: number, parent: EpochEvents) {
    super(parent.raw, roundId, parent);
    this.roundId = roundId;
    this.parent = parent;
  }

  protected slice(
    events: TraceEvent[],
    roundId: number,
    parent: EpochEvents,
  ): [number, number] {
    const epochEvents = parent.events();
    const start =
      indexOfKeyframe(epochEvents, 'roundId', roundId) + parent.startIndex;
    if (start === -1) return [-1, -1];
    const end = indexOfNextKeyframeByKey(events, start, 'roundId') - 1;

    return [start, end];
  }

  steps: () => StepSlice[] | null = () => {
    return null;
  };
}

export class Events extends BaseEvents<null> implements Slice {
  readonly parent = null;

  constructor(raw: TraceEvent[]) {
    super(raw);
  }

  protected slice(events: TraceEvent[]): [number, number] {
    return [0, events.length];
  }

  roundCountEachEpoch(): Map<number, number> {
    const events = this.raw;
    const epochRoundMap: Map<number, number> = new Map();

    let epoch = -1;
    let round = -1;
    for (const event of events) {
      let keyframe = event.keyframe;
      if (!keyframe) continue;
      if ('epochId' in keyframe) {
        const newEpoch = keyframe.epochId;

        // the first epoch appears in events
        if (epoch === -1) {
          epoch = newEpoch;
          continue;
        }

        if (newEpoch - epoch === 1) {
          epochRoundMap.set(epoch, round + 1);
        } else {
          // set round be -1 when the epoch is skipped
          _.range(epoch, newEpoch).forEach(e => epochRoundMap.set(e, -1));
        }

        epoch = newEpoch;
      } else if ('roundId' in keyframe) {
        round = keyframe.roundId;
      }
    }
    return epochRoundMap;
  }

  epoch(epochId: number): EpochEvents {
    return new EpochEvents(this.raw, epochId, this);
  }
}
