import React, { useState } from 'react';
import { withSize, SizeMeProps } from 'react-sizeme';
import { scaleLinear } from 'd3-scale';
import { useStoreState } from '../store';
import { SVGTooltip } from '../component/SVGTooltip';

type EpochTimelineProps = SizeMeProps & {};

export const EpochTimeline = withSize()((props: EpochTimelineProps) => {
  const { slice, activeEpoch } = useStoreState(state => state.tracer);
  const [timeUsage, setTimeUsage] = useState(0);

  if (activeEpoch === null) return null;

  const epoch = slice.epoch(activeEpoch);
  const domain = epoch.timeRange();
  if (!domain) return null;

  const width = props.size.width as number;

  const scale = scaleLinear()
    .domain([0, domain[1] - domain[0]])
    .range([0, width - 50]);

  return (
    <div style={{ whiteSpace: 'nowrap' }}>
      <div>Timeline</div>
      <SVGTooltip content={`${timeUsage}ms`}>
        <div>
          {epoch?.rounds()?.map(round => {
            const timeRange = round.timeRange();
            const roundId = round.roundId;
            if (!timeRange) return null;
            const [start, end] = timeRange;
            const timeUsage = end - start;
            return (
              <svg
                key={roundId}
                width={scale(timeUsage)}
                height={15}
                onMouseOver={() => setTimeUsage(timeUsage)}
              >
                <g key={round.roundId}>
                  <rect
                    x={0}
                    y={0}
                    width="100%"
                    height={15}
                    style={{
                      fill: '#eec360',
                      strokeWidth: 1,
                      stroke: '#333333',
                    }}
                  />
                  <text
                    x="50%"
                    y="50%"
                    dominantBaseline="middle"
                    textAnchor="middle"
                    fill="#333333"
                  >
                    R{round.roundId}
                  </text>
                </g>
              </svg>
            );
          })}
        </div>
      </SVGTooltip>
    </div>
  );
});
